*, :before, :after {
  box-sizing: border-box;
}

html {
  tab-size: 4;
  -webkit-text-size-adjust: 100%;
  line-height: 1.15;
}

body {
  margin: 0;
  font-family: system-ui, AppleSystem, BlinkMacSystemFont, Segoe UI, Roboto, Noto Sans, Ubuntu, Cantarell, Helvetica Neue, AppleSystem, BlinkMacSystemFont, Segoe UI, Roboto, Noto Sans, Ubuntu, Cantarell, Helvetica Neue, -apple-system, Segoe UI, Roboto, Helvetica, Arial, sans-serif, Apple Color Emoji, Segoe UI Emoji;
}

hr {
  height: 0;
  color: inherit;
}

abbr[title] {
  -webkit-text-decoration: underline dotted;
  text-decoration: underline dotted;
}

b, strong {
  font-weight: bolder;
}

code, kbd, samp, pre {
  font-family: ui-monospace, SFMono-Regular, Consolas, Liberation Mono, Menlo, monospace;
  font-size: 1em;
}

small {
  font-size: 80%;
}

sub, sup {
  vertical-align: baseline;
  font-size: 75%;
  line-height: 0;
  position: relative;
}

sub {
  bottom: -.25em;
}

sup {
  top: -.5em;
}

table {
  text-indent: 0;
  border-color: inherit;
}

button, input, optgroup, select, textarea {
  margin: 0;
  font-family: inherit;
  font-size: 100%;
  line-height: 1.15;
}

button, select {
  text-transform: none;
}

button, [type="button"], [type="reset"], [type="submit"] {
  -webkit-appearance: button;
}

::-moz-focus-inner {
  border-style: none;
  padding: 0;
}

:-moz-focusring {
  outline: 1px dotted ButtonText;
}

:-moz-ui-invalid {
  box-shadow: none;
}

legend {
  padding: 0;
}

progress {
  vertical-align: baseline;
}

::-webkit-inner-spin-button, ::-webkit-outer-spin-button {
  height: auto;
}

[type="search"] {
  -webkit-appearance: textfield;
  outline-offset: -2px;
}

::-webkit-search-decoration {
  -webkit-appearance: none;
}

::-webkit-file-upload-button {
  -webkit-appearance: button;
  font: inherit;
}

summary {
  display: list-item;
}

html {
  box-sizing: border-box;
}

*, :after, :before {
  box-sizing: inherit;
}

h1, h2, h3, h4, h5, h6, p {
  margin: 0;
}

ul, li {
  margin: 0;
  padding-left: 0;
  list-style: none;
}

a {
  color: inherit;
  text-decoration: none;
}

img {
  max-width: 100%;
  height: auto;
  display: block;
}

address {
  font-style: normal;
}

.visually-hidden {
  width: 1px;
  height: 1px;
  white-space: nowrap;
  -webkit-clip-path: inset(100%);
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  border: 0;
  margin: -1px;
  padding: 0;
  position: absolute;
  overflow: hidden;
}

.is-hidden {
  opacity: 0;
  visibility: hidden;
  pointer-events: none;
}

:root {
  --colorText-primary: #d41443;
  --colorText-secondary: #fff;
  --colorText-third: #ffa5ba;
  --colorText-other: #000;
  --colorText-accent: #907e82;
  --color-icon: #fea5bb;
  --bgc-primary: #fff5f6;
  --bgc-secondary: #ffb8ca;
  --bgc-modal: #e17992;
  --bgc-btnHover: #ecebeb;
  --bgc-other: #f0d1a5;
  --bgc-accent: #c2e297;
  --border-decorative: #e1e1e1;
  --hover-primary: #f88ca5;
  --hover-secondary: #e8c086;
  --hover-third: #acd17b;
  --bgc-btnHoverAccent: #bd123c;
  --fontFamaly-primary: "Titan One", cursive;
  --fontFamaly-secondary: "DM Sans", sans-serif;
  --default-transition: 250ms cubic-bezier(.4, 0, .2, 1);
}

.container, .gallery-container {
  max-width: 320px;
  width: 100%;
  margin: 0 auto;
  padding: 0 20px;
}

@media screen and (min-width: 768px) {
  .container, .gallery-container {
    max-width: 768px;
    padding: 0 32px;
  }
}

@media screen and (min-width: 1200px) {
  .container, .gallery-container {
    max-width: 1200px;
    padding: 0 16px;
  }
}

.button {
  text-align: center;
  height: 44px;
  font-family: var(--fontFamaly-secondary);
  cursor: pointer;
  transition: background-color var(--default-transition), color var(--default-transition), transform var(--default-transition);
  border: none;
  border-radius: 22px;
  flex-direction: row;
  justify-content: center;
  align-items: center;
  padding: 0;
  font-size: 16px;
  font-style: normal;
  font-weight: 700;
  line-height: 1.31;
  display: flex;
}

.button-arrow:after {
  content: "";
  width: 6px;
  height: 10px;
  background-image: url("Vector1.8cc95434.svg");
  margin-left: 12px;
  display: block;
}

.button__icon {
  width: 6px;
  height: 10px;
  fill: var(--colorText-secondary);
  margin-left: 12px;
}

.button__icon--light {
  fill: var(--colorText-primary);
}

body.modal-open {
  overflow: hidden;
}

.icon-icecream {
  z-index: 19px;
  width: 50px;
  height: 50px;
  cursor: pointer;
  fill: var(--colorText-primary);
  animation: heartBeat 2s infinite;
  position: fixed;
  bottom: 10px;
  left: 10px;
}

@media screen and (min-width: 768px) {
  .icon-icecream {
    width: 70px;
    height: 70px;
    bottom: 30px;
    left: 30px;
  }
}

@media screen and (min-width: 1200px) {
  .icon-icecream {
    width: 70px;
    height: 70px;
    bottom: 50px;
    left: 50px;
  }
}

.icon-icecream:hover {
  animation-play-state: paused;
}

.backdrop-question {
  z-index: 20;
  width: 100%;
  height: 100%;
  background: rgba(0, 0, 0, .2);
  border-radius: 4px;
  transition: opacity .25s cubic-bezier(.4, 0, .2, 1), visibility .25s cubic-bezier(.4, 0, .2, 1);
  position: fixed;
  top: 0;
  left: 0;
}

.question-modal {
  z-index: 21;
  width: 260px;
  height: auto;
  background-color: var(--bgc-modal);
  border: 2px solid var(--color-icon);
  border-radius: 8px;
  padding: 40px;
  transition: transform .25s cubic-bezier(.4, 0, .2, 1);
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%)scale(1);
  box-shadow: 0 8px 30px rgba(212, 20, 67, .5);
}

@media screen and (min-width: 1200px) {
  .question-modal {
    min-width: 528px;
    min-height: 400px;
  }
}

@media screen and (min-width: 768px) {
  .question-modal {
    width: 450px;
  }
}

.backdrop-question.is-hidden .question-modal {
  transform: translate(-50%, -50%)scale(.9);
}

.question-modal__coment {
  text-align: center;
  letter-spacing: .04em;
  text-transform: uppercase;
  font-size: 20px;
  line-height: 1.15;
  font-family: var(--fontFamaly-primary);
  color: var(--colorText-secondary);
}

.question-modal__form {
  background-color: var(--bgc-primary);
  resize: none;
  width: 100%;
  height: 120px;
  border: 1px solid rgba(33, 33, 33, .2);
  border-radius: 4px;
  margin-top: 10px;
  margin-bottom: 20px;
  padding: 12px 16px;
  transition: border .15s cubic-bezier(.39, .41, .69, .69);
}

.question-modal__form::-ms-input-placeholder {
  letter-spacing: .01em;
  color: rgba(117, 117, 117, .5);
  font-size: 12px;
  font-style: normal;
  font-weight: 400;
  line-height: 14px;
}

.question-modal__form::placeholder {
  letter-spacing: .01em;
  color: rgba(117, 117, 117, .5);
  font-size: 12px;
  font-style: normal;
  font-weight: 400;
  line-height: 14px;
}

.question-modal__form:hover, .question-modal__form:focus {
  border: 2px solid var(--bgc-btnHoverAccent);
}

.question-modal .question-modal__title {
  text-align: center;
  margin-bottom: 20px;
  font-size: 26px;
}

@media screen and (min-width: 768px) {
  .question-modal .question-modal__title {
    font-size: 30px;
  }
}

.question-modal__btn {
  max-width: 176px;
  width: 100%;
  height: 44px;
  color: var(--colorText-primary);
  background-color: var(--colorText-secondary);
  transition: transform var(--default-transition), background-color var(--default-transition);
  cursor: pointer;
  margin: 0 auto;
  padding: 0 15px;
  transform: scale(1);
}

@media screen and (min-width: 768px) {
  .question-modal__btn {
    max-width: 220px;
  }
}

@media screen and (min-width: 1200px) {
  .question-modal__btn {
    max-width: 250px;
  }
}

.question-modal__btn:hover, .question-modal__btn:focus {
  background-color: var(--bgc-btnHover);
  transform: scale(1.1);
}

.question-modal__btn-close {
  width: 20px;
  height: 20px;
  cursor: pointer;
  background-color: rgba(0, 0, 0, 0);
  border: none;
  padding: 0;
  position: absolute;
  top: 20px;
  right: 20px;
}

.question-modal__btn-open {
  background-color: rgba(0, 0, 0, 0);
  border: none;
  position: absolute;
  top: 0;
  left: 0;
}

.question-modal__icon {
  stroke: var(--colorText-secondary);
  width: 100%;
  height: 100%;
  transition: stroke var(--default-transition), transform var(--default-transition);
  display: block;
  transform: scale(1);
}

.question-modal-btn-close:hover .question-modal__icon, .question-modal-btn-close:focus .question-modal__icon {
  stroke: var(--colorText-primary);
  transform: scale(1.1);
}

.totop {
  z-index: 5;
  width: 50px;
  height: 50px;
  color: var(--color-icon);
  cursor: pointer;
  box-shadow: inset 0 0 0 4px var(--color-icon);
  opacity: 0;
  visibility: hidden;
  border-radius: 50px;
  transition: all .2s;
  position: fixed;
  bottom: 10px;
  right: 10px;
  overflow: hidden;
  transform: translateY(25px);
}

@media screen and (min-width: 768px) {
  .totop {
    bottom: 30px;
    right: 30px;
  }
}

@media screen and (min-width: 1200px) {
  .totop {
    bottom: 50px;
    right: 50px;
  }
}

.totop:before {
  letter-spacing: .06em;
  text-transform: uppercase;
  font-size: 34px;
  line-height: 1.15;
  font-family: var(--fontFamaly-primary);
  color: var(--colorText-primary);
  width: 100%;
  height: 100%;
  content: "UP";
  justify-content: center;
  align-items: center;
  font-size: 20px;
  transition: transform .25s cubic-bezier(.4, 0, .2, 1);
  display: flex;
  position: absolute;
  top: 0;
  left: 0;
  transform: translateY(100%);
}

.totop:hover:before {
  transform: translateY(0);
}

.totop:after {
  color: var(--colorText-primary);
  width: 95%;
  height: 90%;
  content: "➤";
  justify-content: center;
  align-items: center;
  font-size: 35px;
  transition: transform .25s cubic-bezier(.4, 0, .2, 1);
  display: flex;
  position: absolute;
  top: 0;
  left: 0;
  transform: translateY(0)rotate(-90deg);
}

.totop:hover:after {
  transform: translateY(-100%);
}

.totop--active {
  visibility: visible;
  opacity: 1;
  transform: translateY(0);
}

.totop__svg-path {
  box-sizing: border-box;
  fill: none;
  stroke: var(--colorText-primary);
  stroke-width: 4px;
  transition: all .2s;
}

.logo {
  width: 200px;
  height: 50px;
  transition: transform var(--default-transition);
  display: block;
  transform: scale(1);
}

.logo:hover, .logo:focus {
  transform: scale(1.1);
}

.logo__thumb-icon {
  width: 100%;
  height: 100%;
}

.header-nav {
  z-index: 1;
  background-color: rgba(0, 0, 0, 0);
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
}

.header-nav__thumb {
  justify-content: space-between;
  align-items: center;
  display: flex;
}

@media screen and (min-width: 768px) and (max-width: 1200px) {
  .header-nav__thumb {
    flex-grow: 1;
  }
}

@media screen and (max-width: 768px) {
  .header-nav__outer-margin {
    padding: 8px 0;
  }
}

@media screen and (min-width: 768px) {
  .header-nav__outer-margin {
    padding: 32px 0;
  }
}

@media screen and (min-width: 1200px) {
  .header-nav__outer-margin {
    padding: 40px 0;
  }
}

.menu-thumb {
  display: flex;
}

.button-thumb {
  justify-content: center;
  align-items: center;
  padding: 5px;
  display: flex;
}

@media screen and (min-width: 768px) {
  .button-thumb {
    margin-right: 32px;
  }
}

@media screen and (min-width: 1200px) {
  .button-thumb {
    display: none;
  }
}

.button-menu {
  width: 20px;
  height: 20px;
  cursor: pointer;
  background-color: rgba(0, 0, 0, 0);
  border: none;
  padding: 0;
  display: block;
}

.button-menu__thumb-icon {
  stroke: var(--colorText-primary);
  width: 100%;
  height: 100%;
  transition: stroke var(--default-transition), transform var(--default-transition);
  transform: scale(1);
}

.button-menu:hover .button-menu__thumb-icon, .button-menu:focus .button-menu__thumb-icon {
  stroke: var(--colorText-secondary);
  transform: scale(1.2);
}

@media screen and (min-width: 1200px) {
  .button-menu {
    display: none;
  }
}

.navigation {
  display: none;
}

.navigation__list {
  justify-content: center;
  align-items: center;
  margin-right: 140px;
  display: flex;
}

.navigation__item:not(:last-child) {
  margin-right: 35px;
}

.navigation__link {
  font-family: var(--fontFamaly-secondary);
  color: var(--colorText-secondary);
  transition: color var(--default-transition), transform var(--default-transition);
  align-items: center;
  padding: 8px 0;
  font-size: 16px;
  font-style: normal;
  font-weight: 700;
  line-height: 1.31;
  display: block;
  position: relative;
  transform: scale(1);
}

.navigation__link:hover, .navigation__link:focus {
  color: var(--colorText-primary);
  transform: scale(1.1);
}

@media screen and (min-width: 1200px) {
  .navigation {
    justify-content: center;
    align-items: center;
    display: flex;
  }
}

.button-header {
  width: 160px;
  height: 44px;
  color: var(--colorText-primary);
  background-color: var(--colorText-secondary);
  border: none;
  border-radius: 22px;
  display: none;
  transform: scale(1);
}

.button-header:hover, .button-header:focus {
  background-color: var(--bgc-btnHover);
  transform: scale(1.1);
}

.button-header:after {
  content: "";
  width: 6px;
  height: 10px;
  background-image: url("Vector1.8cc95434.svg");
  margin-left: 12px;
  display: block;
}

@media screen and (min-width: 768px) {
  .button-header {
    cursor: pointer;
    display: flex;
  }
}

.backdrop-mobile.is-hidden {
  visibility: hidden;
  opacity: 0;
  pointer-events: none;
}

.backdrop-mobile.is-hidden .backdrop-mobile__container {
  transform: translateY(-100%);
}

.backdrop-mobile {
  width: 100%;
  height: 1000px;
  cursor: not-allowed;
  visibility: visible;
  opacity: 1;
  z-index: 10;
  background-color: rgba(50, 45, 45, .8);
  transition: visibility .5s cubic-bezier(.4, 0, .2, 1), opacity .5s cubic-bezier(.4, 0, .2, 1);
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
}

.backdrop-mobile__container {
  max-width: 250px;
  width: 100%;
  height: 100vh;
  background: var(--bgc-modal);
  cursor: auto;
  transition: transform 1.6s cubic-bezier(.4, 0, .2, 1);
  position: fixed;
  top: 0;
  right: 0;
  transform: translateY(0);
}

.mobile-menu__button {
  width: 20px;
  height: 20px;
  cursor: pointer;
  background-color: rgba(0, 0, 0, 0);
  border: none;
  padding: 0;
  position: absolute;
  top: 20px;
  right: 20px;
}

.mobile-menu__icon-thumb {
  stroke: var(--colorText-secondary);
  width: 100%;
  height: 100%;
  transition: stroke var(--default-transition), transform var(--default-transition);
  display: block;
  transform: scale(1);
}

.mobile-menu__button:hover .mobile-menu__icon-thumb, .mobile-menu__button:focus .mobile-menu__icon-thumb {
  stroke: var(--colorText-primary);
  transform: scale(1.3);
}

.mobile-menu__icon {
  width: 100%;
  height: 100%;
  display: block;
}

.button-mobile {
  width: 210px;
  height: 44px;
  color: var(--colorText-primary);
  background-color: var(--colorText-secondary);
  transition: transform var(--default-transition), background-color var(--default-transition);
  margin: 0 auto;
  transform: scale(1);
}

@media screen and (min-width: 768px) {
  .button-mobile {
    width: 186px;
  }
}

.button-mobile:hover, .button-mobile:focus {
  background-color: var(--bgc-btnHover);
  transform: scale(1.1);
}

.button-container {
  padding: 60px 20px 20px;
  display: block;
}

.navigation-mobile__list {
  flex-direction: column;
  justify-content: center;
  align-items: flex-start;
  display: flex;
}

.navigation-mobile__item {
  width: 100%;
  border: 1px solid #e18298;
  padding: 12px 20px;
  display: flex;
}

.navigation-mobile__link {
  font-family: var(--fontFamaly-secondary);
  text-align: center;
  color: var(--colorText-secondary);
  transition: color var(--default-transition), font-size var(--default-transition);
  font-size: 14px;
  font-style: normal;
  font-weight: 700;
  line-height: 1.28;
  position: relative;
}

.navigation-mobile__link:hover, .navigation-mobile__link:focus {
  color: var(--colorText-primary);
  font-size: 16px;
}

.backdrop {
  width: 100%;
  height: 100%;
  visibility: visible;
  z-index: 10;
  background-color: rgba(0, 0, 0, .2);
  transition: opacity .25s cubic-bezier(.17, .67, .83, .67), visibility .25s cubic-bezier(.17, .67, .83, .67);
  position: fixed;
  top: 0;
  left: 0;
  overflow-y: scroll;
}

.backdrop.is-hidden .modal-list {
  transform: translate(-50%, -50%)scale(0);
}

.modal-list {
  scrollbar-color: var(--bgc-btnHoverAccent) var(--bgc-primary);
  scrollbar-width: thin;
  max-width: 280px;
  width: 100%;
  height: 250px;
  background-color: var(--bgc-secondary);
  border: 1px solid var(--bgc-modal);
  border-radius: 8px;
  transition: transform .25s cubic-bezier(.17, .67, .83, .67);
  position: absolute;
  top: 50%;
  left: 50%;
  overflow: auto;
  transform: translate(-50%, -50%)scale(1);
  box-shadow: 0 1px 3px rgba(0, 0, 0, .12), 0 1px 1px rgba(0, 0, 0, .14), 0 2px 1px rgba(0, 0, 0, .2);
}

.modal-list__close-button {
  text-align: center;
  height: 30px;
  width: 30px;
  stroke: var(--colorText-primary);
  cursor: pointer;
  transition: stroke var(--default-transition), transform var(--default-transition);
  background-color: #fff;
  border: 1px solid rgba(0, 0, 0, .1);
  border-radius: 50%;
  justify-content: center;
  align-items: center;
  display: flex;
  position: absolute;
  top: 10px;
  right: 10px;
  transform: scale(.9);
}

@media screen and (min-width: 768px) {
  .modal-list__close-button {
    transform: scale(1.1);
  }
}

@media screen and (min-width: 1200px) {
  .modal-list__close-button {
    transform: scale(1.2);
  }
}

.modal-list__close-button:hover, .modal-list__close-button:focus {
  stroke: var(--colorText-other);
  transform: scale(1.1);
}

@media screen and (min-width: 768px) {
  .modal-list__close-button:hover, .modal-list__close-button:focus {
    transform: scale(1.3);
  }
}

@media screen and (min-width: 1200px) {
  .modal-list__close-button:hover, .modal-list__close-button:focus {
    transform: scale(1.5);
  }
}

@media screen and (min-width: 768px) {
  .modal-list {
    max-width: 500px;
    height: 350px;
  }
}

@media screen and (min-width: 1200px) {
  .modal-list {
    max-width: 700px;
    height: 450px;
  }
}

.modal-inner {
  padding: 20px;
}

@media screen and (min-width: 768px) {
  .modal-inner {
    padding: 40px;
  }

  .modal-inner::-webkit-scrollbar {
    width: 24px;
    height: 8px;
    background-color: #143861;
  }

  .modal-inner::-webkit-scrollbar-thumb {
    background-color: #843465;
    border-radius: 9em;
    box-shadow: inset 1px 1px 10px #f3faf7;
  }

  ::-webkit-scrollbar-thumb:hover {
    background-color: #253861;
  }
}

.modal-mobile-buy {
  scrollbar-color: var(--bgc-btnHoverAccent) var(--bgc-primary);
  scrollbar-width: thin;
  max-width: 280px;
  width: 100%;
  max-height: 70vh;
  background-color: var(--bgc-secondary);
  border: 1px solid var(--bgc-modal);
  border-radius: 8px;
  transition: transform .25s cubic-bezier(.17, .67, .83, .67);
  position: absolute;
  top: 50%;
  left: 50%;
  overflow: auto;
  transform: translate(-50%, -50%)scale(1);
  box-shadow: 0 1px 3px rgba(0, 0, 0, .12), 0 1px 1px rgba(0, 0, 0, .14), 0 2px 1px rgba(0, 0, 0, .2);
}

@media screen and (min-width: 768px) {
  .modal-mobile-buy {
    max-width: 450px;
